@import "config/themes/_video-poker";

$primaryBorderRadius: 25px;

.root {
  background: linear-gradient(276.53deg, #2e2eed -93.34%, #4e4ef9 82.6%);
  color: white;
  font-size: 18px;
  line-height: 22px;
  font-weight: bold;
  border-radius: $primaryBorderRadius;
  padding-left: 37px;
  padding-right: 37px;
  padding-top: 6px;
  padding-bottom: 6px;
  min-height: 38px;
}

.dealButton {
  background: transparent;
  border: 2px $slate-blue solid;
  color: use(--template-statisticsBlock-tab-color);
}

.buttonWrapper {
  display: flex;
  width: 100%;
  justify-content: center;
  margin-top: 20px;
}
