@import "sass/index";

.root {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  row-gap: 5px;
  column-gap: 10px;
  padding: 10px;
  width: 100%;
  border-radius: 8px;
  background-color: use(--template-gameCell-controller);
  box-shadow: 0px 24px 30px rgba(0, 0, 0, 0.19758);
  @media (max-width: 580px) {
    column-gap: 5px;
    padding: 5px;
  }
}
