.fieldWrapper {
  width: 100%;
  margin-top: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.field {
  display: flex;
  flex-wrap: wrap;
  width: 720px;
  flex-shrink: 0;
  justify-content: space-between;
  @media (max-width: 1130px) {
    width: 100%;
  }
  @media (max-width: 810px) {
    width: 720px;
  }
}
