@import "sass/index";
@import "config/themes/_video-poker";

.name {
  display: inline-block;
  text-transform: uppercase;
  background-color: use(--template-anyContent-colors-five);
  padding-left: 20px;
  border: 2px solid transparent;
  @media (max-width: 580px) {
    padding-left: 10px;
  }
}

.ratio {
  display: inline-block;
  text-align: center;
  background-color: rgba($white, 0.1);
  border: 2px solid var(--template-anyContent-colors-first);
}

.amount {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: 2px solid #{use(--template-anyContent-colors-second)};
}

.fontStyle {
  padding-top: 3px;
  padding-bottom: 3px;
  border-radius: 6px;
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
  color: use(--template-statisticsBlock-tab-color);
  user-select: none;
  @media (max-width: 580px) {
    font-size: 12px;
  }
}

.selected {
  background-color: #7ad25b !important;
  color: #1f1f43;
}

.coinIcon {
  position: static;
  width: 22px;
  height: 22px;
  margin-left: 5px;
  @media (max-width: 580px) {
    width: 12px;
    height: 12px;
    margin-left: 2px;
  }
}
