@import "../CardFront/style.module.scss";

.cardBack {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: $cardWidth;
  height: $cardHeight;
  background-color: #412968;
  border-radius: 9px;
  flex-shrink: 0;
  backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  transform: rotateY(180deg);
  & > svg:first-child {
    position: absolute;
  }
}
