@import "../CardFront/style.module.scss";

.cardWrapper {
  position: relative;
  width: $cardWidth;
  height: $cardHeight;
  flex-shrink: 0;
  transform-style: preserve-3d;
  transform: rotateY(180deg);
}

.showCard {
  transform: rotateY(0deg);
  animation-name: openCard;
  animation-duration: 0.6s;
}

.rotate {
  animation-name: rotate;
  animation-duration: 1.2s;
}

@keyframes rotate {
  from {
    transform: rotateY(0deg);
  }
  50% {
    transform: rotateY(180deg);
  }
  to {
    transform: rotateY(0deg);
  }
}

@keyframes openCard {
  from {
    transform: rotateY(180deg);
  }
  to {
    transform: rotateY(0deg);
  }
}
