@import "config/themes/_video-poker";

$cardWidth: 120px;
$cardHeight: 169px;

:export {
  #{"cardWidth"}: $cardWidth;
  #{"cardHeight"}: $cardHeight;
}

.card {
  position: absolute;
  width: $cardWidth;
  height: $cardHeight;
  background-color: #f2f2f2;
  border-radius: 9px;
  flex-shrink: 0;
  padding: 8px;
  box-shadow: 0px 2.14286px 14.5714px rgba(0, 0, 0, 0.1);
  filter: drop-shadow(0px 12.5406px 29.8587px rgba(0, 0, 0, 0.15));
  transition: transform 0.2s, border 0.2s;
  cursor: pointer;
  backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  &:hover {
    transform: translateY(-4px);
  }
}

.selected {
  border: 3px $slate-blue solid;
}

.combination {
  border: 4px #2d9600 solid;
}

.lose {
  filter: brightness(50%);
}

.hold {
  display: inline-block;
  opacity: 0;
  position: absolute;
  font-size: 12px;
  font-weight: bold;
  line-height: 16px;
  color: white;
  font-family: "Gilroy";
  padding-left: 14px;
  padding-right: 14px;
  padding-top: 2px;
  padding-bottom: 2px;
  background-color: $slate-blue;
  border-radius: 14px;
  left: 28px;
  bottom: 155px;
  transition: opacity 0.2s;
}

.holdActive {
  opacity: 1;
}

.cardValue {
  position: relative;
  right: 3px;
  display: inline-block;
  width: 22px;
  text-align: center;
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 21px;
  line-height: 25px;
  user-select: none;
}

.cardValueRight {
  left: 3px;
  right: 0;
}

.suitSmall {
  display: block;
  width: 16px;
  height: 16px;
  transform: scale(2);
}

.cardValueSuitContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.suitContainerRight {
  align-items: flex-end;
}

.suitBig {
  text-align: center;
  margin-top: 10px;
  & > svg {
    width: 58px;
    height: 58px;
    transform: scale(2);
  }
}

.diamond {
  color: #bf0000;
  fill: #bf0000;
}

.trefle {
  color: #0e007d;
  fill: #0e007d;
}

.heart {
  color: #bf0000;
  fill: #bf0000;
}

.pique {
  color: #0e007d;
  fill: #0e007d;
}
